import React from 'react';
import { useAppContext } from '../context/AppContext';
import { STYLE, formatRp } from '../data/initialData';

const PembiayaanPage = () => {
    const { dataPembiayaan, setDataPembiayaan, user, setShowLoginModal, getTotalPembiayaan } = useAppContext();
    const { penerimaan, pengeluaran, neto } = getTotalPembiayaan();

    const handleUpdate = (cat, id, val) => {
        if(!user) { setShowLoginModal(true); return; }
        
        // Logika agar inputan 0 tidak mengganggu, dan bisa dihapus jadi kosong
        const num = val === '' ? 0 : parseFloat(val);
        
        setDataPembiayaan(prev => ({ 
            ...prev, 
            [cat]: prev[cat].map(p => p.id === id ? { ...p, anggaran: num } : p) 
        }));
    };

    return (
        <div style={STYLE.card}>
            <h3 style={{color:'#8e44ad', borderBottom:'2px solid #ddd', paddingBottom:'10px'}}> 6. PEMBIAYAAN DESA</h3>
            
            {/* BAGIAN 1: PENERIMAAN */}
            <h4 style={{color:'#2980b9', marginTop:'20px', marginBottom:'10px'}}>6.1. PENERIMAAN PEMBIAYAAN</h4>
            <table style={STYLE.rekapTable}>
                <thead>
                    <tr style={{background:'#eee', fontWeight:'bold'}}>
                        <th style={{padding:'8px'}}>Kode</th>
                        <th>Uraian</th>
                        <th style={{textAlign:'right', width:'150px'}}>Anggaran (Rp)</th>
                    </tr>
                </thead>
                <tbody>
                    {dataPembiayaan.penerimaan.map(item => (
                        <tr key={item.id} style={{borderBottom:'1px solid #ccc'}}>
                            <td style={{padding:'8px', fontWeight:'bold'}}>{item.id}</td>
                            <td>{item.nama}</td>
                            <td style={{textAlign:'right', padding:'5px'}}>
                                <input 
                                    type="number" 
                                    value={item.anggaran === 0 ? '' : item.anggaran} // Agar angka 0 tidak perlu dihapus manual
                                    placeholder="0"
                                    onChange={e => handleUpdate('penerimaan', item.id, e.target.value)} 
                                    style={{width:'100%', padding:'8px', textAlign:'right', borderRadius:'4px', border:'1px solid #ddd'}}
                                />
                            </td>
                        </tr>
                    ))}
                    <tr style={{background:'#d6eaf8', fontWeight:'bold'}}>
                        <td colSpan="2" style={{textAlign:'right', padding:'10px'}}>TOTAL PENERIMAAN</td>
                        <td style={{textAlign:'right', padding:'10px'}}>{formatRp(penerimaan)}</td>
                    </tr>
                </tbody>
            </table>

            {/* BAGIAN 2: PENGELUARAN */}
            <h4 style={{color:'#e74c3c', marginTop:'30px', marginBottom:'10px'}}>6.2. PENGELUARAN PEMBIAYAAN</h4>
            <table style={STYLE.rekapTable}>
                 <thead>
                    <tr style={{background:'#eee', fontWeight:'bold'}}>
                        <th style={{padding:'8px'}}>Kode</th>
                        <th>Uraian</th>
                        <th style={{textAlign:'right', width:'150px'}}>Anggaran (Rp)</th>
                    </tr>
                </thead>
                <tbody>
                    {dataPembiayaan.pengeluaran.map(item => (
                        <tr key={item.id} style={{borderBottom:'1px solid #ccc'}}>
                            <td style={{padding:'8px', fontWeight:'bold'}}>{item.id}</td>
                            <td>{item.nama}</td>
                            <td style={{textAlign:'right', padding:'5px'}}>
                                <input 
                                    type="number" 
                                    value={item.anggaran === 0 ? '' : item.anggaran} 
                                    placeholder="0"
                                    onChange={e => handleUpdate('pengeluaran', item.id, e.target.value)} 
                                    style={{width:'100%', padding:'8px', textAlign:'right', borderRadius:'4px', border:'1px solid #ddd'}}
                                />
                            </td>
                        </tr>
                    ))}
                    <tr style={{background:'#fadbd8', fontWeight:'bold'}}>
                        <td colSpan="2" style={{textAlign:'right', padding:'10px'}}>TOTAL PENGELUARAN</td>
                        <td style={{textAlign:'right', padding:'10px'}}>{formatRp(pengeluaran)}</td>
                    </tr>
                </tbody>
            </table>

            {/* HASIL NETO */}
            <div style={{
                background: neto >= 0 ? '#d5f5e3' : '#fdedec', 
                border: `1px solid ${neto >= 0 ? '#27ae60' : '#c0392b'}`,
                padding:'15px', 
                marginTop:'20px', 
                borderRadius:'8px',
                display:'flex', 
                justifyContent:'space-between', 
                fontWeight:'bold',
                fontSize:'16px'
            }}>
                <span>PEMBIAYAAN NETO (Penerimaan - Pengeluaran)</span>
                <span style={{color: neto >= 0 ? '#27ae60' : '#c0392b'}}>{formatRp(neto)}</span>
            </div>
        </div>
    );
};

export default PembiayaanPage;