import React, { useState, useEffect, createContext, useContext } from 'react';
// Perhatikan: Kita pakai setDoc, bukan updateDoc biar lebih kuat
import { db } from '../firebaseConfig'; 
import { doc, onSnapshot, setDoc } from 'firebase/firestore';

import { INITIAL_BELANJA, INITIAL_PENDAPATAN, INITIAL_PEMBIAYAAN } from '../data/initialData';
import SSH_DATA from '../data/data_ssh'; 

const AppContext = createContext();

// --- HELPER FUNCTIONS ---
const getOutputTotal = (out) => out.rabs ? out.rabs.reduce((a,b)=>a+b.total,0) : 0;
const getKegiatanTotal = (keg) => keg.outputs ? keg.outputs.reduce((a,b)=>a+getOutputTotal(b),0) : 0;
const getSubTotal = (sub) => sub.kegiatan ? sub.kegiatan.reduce((a,b)=>a+getKegiatanTotal(b),0) : 0;
const getBidangTotal = (bid) => bid.subs ? bid.subs.reduce((a,b)=>a+getSubTotal(b),0) : 0;

export const useAppContext = () => useContext(AppContext);

export const AppProvider = ({ children }) => {
    const [user, setUser] = useState(() => {
        const saved = localStorage.getItem('rapbdes_user');
        return saved ? JSON.parse(saved) : null;
    });
    const [showLoginModal, setShowLoginModal] = useState(false);

    // --- STATE DATA ---
    const [dataBelanja, setDataBelanja] = useState(INITIAL_BELANJA);
    const [dataPendapatan, setDataPendapatan] = useState(INITIAL_PENDAPATAN);
    const [dataPembiayaan, setDataPembiayaan] = useState(INITIAL_PEMBIAYAAN);
    
    const [dataKey, setDataKey] = useState(Date.now()); 
    const [dataSSH] = useState(SSH_DATA || []);
    const [loading, setLoading] = useState(true); 

    // --- 1. KONEKSI KE DATABASE (DENGAR LIVE) ---
    useEffect(() => {
        const docRef = doc(db, "desa", "apbdes2026");

        const unsubscribe = onSnapshot(docRef, (docSnap) => {
            if (docSnap.exists()) {
                const data = docSnap.data();
                console.log(" Data masuk dari Cloud:", data); // Cek di Console
                if(data.belanja) setDataBelanja(data.belanja);
                if(data.pendapatan) setDataPendapatan(data.pendapatan);
                if(data.pembiayaan) setDataPembiayaan(data.pembiayaan);
            } else {
                // Jika kosong, inisialisasi
                setDoc(docRef, {
                    belanja: INITIAL_BELANJA,
                    pendapatan: INITIAL_PENDAPATAN,
                    pembiayaan: INITIAL_PEMBIAYAAN
                });
            }
            setLoading(false);
            setDataKey(Date.now());
        }, (error) => {
            console.error(" Gagal mengambil data:", error);
            alert("Koneksi Database Putus! Cek internet.");
        });

        return () => unsubscribe();
    }, []);

    // --- 2. FUNGSI SIMPAN (LEBIH KUAT) ---
    const saveDataToCloud = async (newBelanja, newPendapatan, newPembiayaan) => {
        const docRef = doc(db, "desa", "apbdes2026");
        const payload = {};
        if(newBelanja) payload.belanja = newBelanja;
        if(newPendapatan) payload.pendapatan = newPendapatan;
        if(newPembiayaan) payload.pembiayaan = newPembiayaan;

        try {
            // Kita pakai setDoc dengan {merge: true}
            // Artinya: Kalau data belum ada, buat baru. Kalau sudah ada, update.
            await setDoc(docRef, payload, { merge: true });
            console.log(" Berhasil simpan ke Cloud");
        } catch (e) {
            console.error(" GAGAL SIMPAN:", e);
            alert("GAGAL MENYIMPAN KE SERVER!\nError: " + e.message);
        }
    };

    // --- UPDATE FUNCTIONS ---

    const updateRAB = (bidId, subId, kegId, outId, newRabs) => {
        const newData = [...dataBelanja];
        const b = newData.find(x=>x.id===bidId);
        if(b) {
            const s = b.subs.find(x=>x.id===subId);
            if(s) {
                const k = s.kegiatan.find(x=>x.id===kegId);
                if(k) {
                    const o = k.outputs.find(x=>x.id===outId);
                    if(o) { 
                        o.rabs = newRabs; 
                        setDataBelanja(newData); 
                        saveDataToCloud(newData, null, null); 
                    }
                }
            }
        }
    };

    const addOutput = (bidId, subId, kegId, uraian) => {
        const newData = [...dataBelanja];
        const b = newData.find(x => x.id === bidId);
        if (b) {
            const s = b.subs.find(x => x.id === subId);
            if (s) {
                const k = s.kegiatan.find(x => x.id === kegId);
                if (k) {
                    k.outputs.push({
                        id: Date.now().toString(),
                        uraian: uraian,
                        sat: 'Paket',
                        rabs: []
                    });
                    setDataBelanja(newData);
                    saveDataToCloud(newData, null, null);
                }
            }
        }
    };

    const resetData = () => {
        if(window.confirm("RESET SERVER: Yakin hapus semua data online?")) {
            const emptyBelanja = INITIAL_BELANJA;
            const emptyPendapatan = INITIAL_PENDAPATAN;
            const emptyPembiayaan = INITIAL_PEMBIAYAAN;
            
            setDataBelanja(emptyBelanja);
            setDataPendapatan(emptyPendapatan);
            setDataPembiayaan(emptyPembiayaan);
            
            saveDataToCloud(emptyBelanja, emptyPendapatan, emptyPembiayaan);
            window.location.reload();
        }
    };

    const login = (u, p) => {
        if(u === 'Admin' && p === '123') {
            const data = { username: 'Admin' };
            setUser(data);
            localStorage.setItem('rapbdes_user', JSON.stringify(data));
            setShowLoginModal(false);
            return true;
        }
        return false;
    };

    const logout = () => {
        setUser(null);
        localStorage.removeItem('rapbdes_user');
    };

    const getTotalBelanja = () => dataBelanja ? dataBelanja.reduce((a,b)=>a+getBidangTotal(b),0) : 0;
    const getTotalPendapatan = () => dataPendapatan ? dataPendapatan.reduce((a,b)=>a+b.anggaran,0) : 0;
    
    const getTotalPembiayaan = () => {
        const penerimaan = dataPembiayaan?.penerimaan ? dataPembiayaan.penerimaan.reduce((a,b)=>a+b.anggaran,0) : 0;
        const pengeluaran = dataPembiayaan?.pengeluaran ? dataPembiayaan.pengeluaran.reduce((a,b)=>a+b.anggaran,0) : 0;
        return { penerimaan, pengeluaran, neto: penerimaan - pengeluaran };
    };

    const savePendapatan = (newData) => {
        setDataPendapatan(newData);
        saveDataToCloud(null, newData, null);
    }
    const savePembiayaan = (newData) => {
        setDataPembiayaan(newData);
        saveDataToCloud(null, null, newData);
    }

    return (
        <AppContext.Provider value={{ 
            user, showLoginModal, setShowLoginModal, login, logout, resetData, loading,
            dataBelanja, setDataBelanja,
            dataPendapatan, setDataPendapatan: savePendapatan, 
            dataPembiayaan, setDataPembiayaan: savePembiayaan,
            dataSSH, 
            getTotalBelanja, getTotalPendapatan, getTotalPembiayaan,
            getSubTotal, getKegiatanTotal, getOutputTotal, getBidangTotal,
            updateRAB, addOutput, dataKey
        }}>
            {children}
        </AppContext.Provider>
    );
};