import React, { useState } from 'react';
import { useAppContext } from '../context/AppContext';
import { STYLE } from '../data/initialData';

const LoginModal = () => {
    const { showLoginModal, setShowLoginModal, login } = useAppContext();
    const [u, setU] = useState('');
    const [p, setP] = useState('');
    
    // Jika tidak disuruh muncul, jangan render apa-apa
    if(!showLoginModal) return null;
    
    return (
        <div style={STYLE.modalOverlay}>
            <div style={STYLE.modalContent}>
                <h3 style={{marginTop:0, color:'#2c3e50'}}> LOGIN SISTEM</h3>
                <div style={{marginBottom:'15px', color:'#666', fontSize:'12px'}}>User: Admin | Pass: 123</div>
                <input 
                    placeholder="Username" 
                    onChange={e=>setU(e.target.value)} 
                    style={{display:'block', width:'90%', padding:'10px', marginBottom:'10px', borderRadius:'4px', border:'1px solid #ddd'}}
                />
                <input 
                    type="password" 
                    placeholder="Password" 
                    onChange={e=>setP(e.target.value)} 
                    style={{display:'block', width:'90%', padding:'10px', marginBottom:'10px', borderRadius:'4px', border:'1px solid #ddd'}}
                />
                <div style={{textAlign:'right'}}>
                    <button onClick={()=>setShowLoginModal(false)} style={{...STYLE.btn, background:'#95a5a6', marginRight:'10px'}}>BATAL</button>
                    <button onClick={()=>login(u,p)} style={{...STYLE.btn, background:'#3498db', color:'white'}}>MASUK</button>
                </div>
            </div>
        </div>
    )
}

export default LoginModal;