import React from 'react';
import { useAppContext } from '../context/AppContext';
import { formatRp } from '../data/initialData';

const LaporanCetak = () => {
    // Kita ambil fungsi hitung-hitungan yang sudah kita taruh di Context
    const { 
        dataPendapatan, dataBelanja, 
        getTotalPendapatan, getTotalBelanja, getTotalPembiayaan, 
        getKegiatanTotal 
    } = useAppContext();

    const { penerimaan, pengeluaran, neto } = getTotalPembiayaan();
    const totPen = getTotalPendapatan();
    const totBel = getTotalBelanja();
    const surplus = totPen - totBel;
    const silpa = surplus + neto;

    return (
        <div className="print-only-area" style={{display:'none'}}>
            <div style={{textAlign:'center', fontWeight:'bold', fontSize:'12pt', marginBottom:'20px', borderBottom:'3px double black', paddingBottom:'15px'}}>PEMERINTAH DESA NGADISONO<br/>RENCANA ANGGARAN PENDAPATAN DAN BELANJA DESA<br/>TAHUN ANGGARAN 2026</div>
            <table className="print-table">
                <thead><tr><th style={{width:'80px'}}>KODE</th><th>URAIAN</th><th style={{width:'150px'}}>ANGGARAN (Rp)</th></tr></thead>
                <tbody>
                    <tr className="bold-row"><td>4.</td><td>PENDAPATAN</td><td></td></tr>
                    {dataPendapatan.map(p => <tr key={p.id}><td>{p.id}</td><td style={{paddingLeft:'10px'}}>{p.nama}</td><td className="angka">{formatRp(p.anggaran)}</td></tr>)}
                    <tr className="total-row"><td></td><td style={{textAlign:'center'}}>JUMLAH PENDAPATAN</td><td className="angka">{formatRp(totPen)}</td></tr>
                    
                    <tr className="bold-row"><td>5.</td><td>BELANJA</td><td></td></tr>
                    {dataBelanja.map(bid => (
                        <React.Fragment key={bid.id}>
                            <tr className="bold-row"><td>{bid.kode}</td><td>{bid.nama}</td><td></td></tr>
                            {bid.subs.map(sub => (
                                <React.Fragment key={sub.id}>
                                    <tr className="bold-row"><td>{sub.kode}</td><td style={{paddingLeft:'10px'}}>{sub.nama}</td><td></td></tr>
                                    {sub.kegiatan && sub.kegiatan.map(keg => (
                                        <tr key={keg.id}><td>{keg.kode}</td><td style={{paddingLeft:'20px'}}>{keg.nama}</td><td className="angka">{formatRp(getKegiatanTotal(keg))}</td></tr>
                                    ))}
                                </React.Fragment>
                            ))}
                        </React.Fragment>
                    ))}
                    <tr className="total-row"><td></td><td style={{textAlign:'center'}}>JUMLAH BELANJA</td><td className="angka">{formatRp(totBel)}</td></tr>
                    <tr className="total-row"><td></td><td style={{textAlign:'center'}}>SURPLUS / (DEFISIT)</td><td className="angka">{formatRp(surplus)}</td></tr>
                    
                    <tr className="bold-row"><td>6.</td><td>PEMBIAYAAN</td><td></td></tr>
                    <tr className="bold-row"><td>6.1.</td><td>Penerimaan Pembiayaan</td><td className="angka">{formatRp(penerimaan)}</td></tr>
                    <tr className="bold-row"><td>6.2.</td><td>Pengeluaran Pembiayaan</td><td className="angka">{formatRp(pengeluaran)}</td></tr>
                    <tr className="total-row"><td></td><td style={{textAlign:'center'}}>PEMBIAYAAN NETO</td><td className="angka">{formatRp(neto)}</td></tr>
                    <tr className="total-row" style={{border:'2px solid black'}}><td></td><td style={{textAlign:'center'}}>SiLPA</td><td className="angka">{formatRp(silpa)}</td></tr>
                </tbody>
            </table>
        </div>
    )
}

export default LaporanCetak;