import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import { useAppContext } from '../context/AppContext';
import { STYLE, formatRp } from '../data/initialData';
import RabModal from '../components/RabModal';

const BelanjaPage = () => {
    const { bidId } = useParams(); 
    const { dataBelanja, user, setShowLoginModal, getOutputTotal, addOutput } = useAppContext();
    const [expandedSubs, setExpandedSubs] = useState({});
    const [expandedKegs, setExpandedKegs] = useState({}); 
    const [editRAB, setEditRAB] = useState(null);
    
    const bid = dataBelanja.find(x => x.id === bidId);
    
    if(!bid) return <div style={STYLE.card}><h3>Data tidak ditemukan</h3></div>;

    const toggleSub = (id) => setExpandedSubs(prev => ({...prev, [id]: !prev[id]}));
    const toggleKeg = (id) => setExpandedKegs(prev => ({...prev, [id]: !prev[id]}));

    return (
        <div>
            {editRAB && <RabModal data={editRAB} onClose={()=>setEditRAB(null)} />}
            <div style={STYLE.card}><h3>{bid.kode} {bid.nama}</h3></div>
            {bid.subs.map(sub => (
                <div key={sub.id} style={{marginBottom:'10px'}}>
                    <div style={{...STYLE.header, background: expandedSubs[sub.id]?'#2980b9':'#34495e', color:'white'}} onClick={()=>toggleSub(sub.id)}>
                        <span>{sub.kode} {sub.nama}</span>
                    </div>
                    {expandedSubs[sub.id] && (
                        <div style={STYLE.body}>
                            <table style={STYLE.rekapTable}>
                                <tbody>
                                    {sub.kegiatan && sub.kegiatan.map(keg => (
                                        <React.Fragment key={keg.id}>
                                            <tr style={{borderBottom:'1px dashed #eee', cursor:'pointer'}} onClick={()=>toggleKeg(keg.id)}>
                                                <td style={{fontWeight:'bold', padding:'8px'}}>{keg.kode}</td><td style={{padding:'8px'}}>{keg.nama}</td><td style={{textAlign:'center'}}><span style={{background:'#f39c12', color:'white', padding:'4px 8px', borderRadius:'4px', fontSize:'11px'}}>{expandedKegs[keg.id]?'TUTUP':'LIHAT'}</span></td>
                                            </tr>
                                            {expandedKegs[keg.id] && (
                <tr style={{background:'#fcfcfc'}}>
                    <td colSpan="3" style={{padding:'15px'}}>
                        {/* TABEL OUTPUT */}
                        <table style={{width:'100%', borderCollapse:'collapse', marginBottom:'10px'}}>
                            <tbody>
                                {keg.outputs.map(out => (
                                    <tr key={out.id} style={{borderBottom:'1px dashed #eee'}}>
                                        <td style={{padding:'5px'}}>{out.uraian}</td>
                                        <td style={{textAlign:'right', fontWeight:'bold', width:'150px'}}>{formatRp(getOutputTotal(out))}</td>
                                        <td style={{textAlign:'center', width:'80px'}}>
                                            <button 
                                                style={{...STYLE.btn, background:'#00b894', color:'white', padding:'4px 8px', fontSize:'11px'}} 
                                                onClick={() => { 
                                                    if(!user) setShowLoginModal(true); 
                                                    else setEditRAB({ bidId: bid.id, subId: sub.id, kegId: keg.id, output: out }); 
                                                }}
                                            >
                                                RAB
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>

                        {/* TOMBOL TAMBAH OUTPUT BARU */}
                        <div style={{textAlign:'right'}}>
                            <button 
                                style={{...STYLE.btn, background:'#34495e', color:'white', fontSize:'11px', padding:'6px 12px'}}
                                onClick={() => {
                                    if(!user) {
                                        setShowLoginModal(true);
                                    } else {
                                        const nama = prompt("Masukkan Nama Output Baru:");
                                        if(nama) {
                                            addOutput(bid.id, sub.id, keg.id, nama);
                                        }
                                    }
                                }}
                            >
                                + TAMBAH OUTPUT
                            </button>
                        </div>
                    </td>
                </tr>
            )}
                                        </React.Fragment>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>
            ))}
        </div>
    )
}

export default BelanjaPage;