import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAppContext } from '../context/AppContext';
import { STYLE } from '../data/initialData';

const Sidebar = () => {
    const navigate = useNavigate();
    const { user, setShowLoginModal, resetData } = useAppContext();
    const [isOpen, setIsOpen] = useState(false); // Status menu: Buka atau Tutup

    // Fungsi agar setelah klik menu, dropdown otomatis menutup (biar rapi)
    const handleNav = (path) => {
        navigate(path);
        setIsOpen(false); 
    };

    return (
        <div style={STYLE.sidebar} className="hide-on-print sidebar-responsive">
            
            {/* HEADER KHUSUS HP (Judul + Tombol Hamburger) */}
            <div className="mobile-header">
                <div style={{fontWeight:'bold', fontSize:'1.2rem'}}> RAPBDes (Online)</div>
                <button 
                    onClick={() => setIsOpen(!isOpen)} 
                    style={{background:'transparent', border:'none', color:'white', fontSize:'24px', cursor:'pointer'}}
                >
                    {isOpen ? '' : ''}
                </button>
            </div>

            {/* HEADER DESKTOP (Hanya tampil di Laptop) */}
            <div style={STYLE.sidebarHeader} className="desktop-header">
                 RAPBDes (Online)
            </div>
            
            {/* DAFTAR MENU (Tampil jika di Laptop ATAU jika isOpen=True di HP) */}
            <div 
                style={{flex:1, padding:'10px 0', overflowY:'auto'}} 
                className={`menu-list-responsive ${isOpen ? 'show-menu' : ''}`}
            >
                <div style={STYLE.menuSection}>UTAMA</div>
                <div style={STYLE.menuItem} onClick={()=>handleNav('/')}> Dashboard</div>
                
                <div style={STYLE.menuSection}>INPUT</div>
                <div style={STYLE.menuItem} onClick={()=>handleNav('/pendapatan')}> Pendapatan</div>
                <div style={STYLE.menuItem} onClick={()=>handleNav('/pembiayaan')}> Pembiayaan</div>
                
                <div style={STYLE.menuSection}>BELANJA</div>
                <div style={STYLE.menuItem} onClick={()=>handleNav('/belanja/01')}>1 Pemerintahan</div>
                <div style={STYLE.menuItem} onClick={()=>handleNav('/belanja/02')}>2 Pembangunan</div>
                <div style={STYLE.menuItem} onClick={()=>handleNav('/belanja/03')}>3 Pembinaan</div>
                <div style={STYLE.menuItem} onClick={()=>handleNav('/belanja/04')}>4 Pemberdayaan</div>
                <div style={STYLE.menuItem} onClick={()=>handleNav('/belanja/05')}>5 Bencana</div>

                {/* Tombol Login/Reset juga masuk ke dropdown */}
                <div style={{padding:'20px', borderTop:'1px solid rgba(255,255,255,0.1)', marginTop:'10px'}}>
                    <button onClick={()=>{window.print(); setIsOpen(false);}} style={{...STYLE.btn, background:'#00b894', color:'white', width:'100%', marginBottom:'10px'}}> CETAK</button>
                    {user ? 
                        <button onClick={()=>{resetData(); setIsOpen(false);}} style={{...STYLE.btn, background:'#e74c3c', width:'100%'}}>RESET DATA</button> : 
                        <button onClick={()=>{setShowLoginModal(true); setIsOpen(false);}} style={{...STYLE.btn, background:'#3498db', width:'100%'}}>LOGIN ADMIN</button>
                    }
                </div>
            </div>
        </div>
    )
}

export default Sidebar;
