import React from 'react';
import { useAppContext } from '../context/AppContext';
import { STYLE, formatRp } from '../data/initialData';

const PendapatanPage = () => {
    const { dataPendapatan, setDataPendapatan, user, setShowLoginModal, getTotalPendapatan } = useAppContext();
    const update = (id, val) => {
        if(!user) { setShowLoginModal(true); return; }
        const num = parseFloat(val.replace(/[^0-9]/g, '')) || 0;
        setDataPendapatan(prev => prev.map(p => p.id === id ? { ...p, anggaran: num } : p));
    }
    return (
        <div style={STYLE.card}>
            <h3 style={{color:'#27ae60', borderBottom:'2px solid #ddd', paddingBottom:'10px'}}> 4. PENDAPATAN DESA</h3>
            <table style={{...STYLE.rekapTable, border:'1px solid #ccc'}}>
                <thead><tr style={{background:'#27ae60', color:'white'}}><th>KODE</th><th>URAIAN</th><th style={{textAlign:'right'}}>JUMLAH (Rp)</th></tr></thead>
                <tbody>
                    {dataPendapatan.map(p => (
                        <tr key={p.id} style={{borderBottom:'1px solid #eee'}}>
                            <td style={{padding:'10px', fontWeight:'bold'}}>{p.id}</td><td>{p.nama}</td>
                            <td style={{padding:'10px'}}><input type="text" value={p.anggaran.toLocaleString('id-ID')} onChange={e=>update(p.id, e.target.value)} style={{width:'100%', padding:'8px', textAlign:'right', fontWeight:'bold'}} /></td>
                        </tr>
                    ))}
                    <tr style={{background:'#d5f5e3', fontWeight:'bold'}}><td colSpan="2" style={{textAlign:'right', padding:'10px'}}>TOTAL PENDAPATAN</td><td style={{textAlign:'right', padding:'10px', fontSize:'16px'}}>{formatRp(getTotalPendapatan())}</td></tr>
                </tbody>
            </table>
        </div>
    );
};

export default PendapatanPage;